
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { validateNotNull, validatePhone } from 'util/rules'
import Scroll from 'assembly/scroll/scroll'
import Timer from 'assembly/timer/timer'
import localStore from 'util/localStore'
export default {
data () {
return {
phone: '',
secretPhone: '',
mobile: '',
passwd: '',
repasswd: '',
code: '',
inited: false
}
},
methods: {
sendCode () {
if (!this.$refs.timer.disabled) { // 如果没在锁定状态
if (this.mobile === this.secretPhone) {
this.$refs.timer.start()
this.$tupHttp(this, 'front/login/send?mobile=' + this.phone).then(res => {
this.$vux.toast.text('验证码已发送')
})
} else {
if (validatePhone(this, this.mobile)) {
this.$refs.timer.start()
this.$tupHttp(this, 'front/login/send?mobile=' + this.mobile).then(res => {
this.$vux.toast.text('验证码已发送')
})
}
}
}
},
send () {
if (this.inited) {
return
}
let tel = this.mobile === this.secretPhone ? this.phone : this.mobile
if (validatePhone(this, tel) && validateNotNull(this, this.code, '验证码') && validateNotNull(this, this.passwd, '密码') && this.validateRepeatPsw()) {
this.inited = true
this.$tupHttp(this, 'front/user/updateLoginPasswordRequest', 'POST', {
clientId: localStore.get('TUP_pUid'),
code: this.code,
mobile: tel,
newPassword: this.passwd
}).then(res => {
this.$vux.toast.text('密码修改成功,请重新登录')
localStore.remove('TUP_pUid')
localStore.remove('TUP_token')
setTimeout(() => {
this.$router.go(-1)
}, 1000)
}, () => {
this.inited = false
})
}
},
validateRepeatPsw () {
let pattern = /^[\w]{6,20}$/
if (this.passwd === '111111') {
this.$vux.toast.text('新密码不可与初始密码一致')
return false
}
if (!pattern.test(this.passwd)) {
this.$vux.toast.text('密码需为数字、字母或组合，6-20位')
return false
}
if (!this.repasswd) {
this.$vux.toast.text('请输入确认密码')
return false
}
if (this.passwd !== this.repasswd) {
this.$vux.toast.text('两次输入密码不一致')
return false
}
return true
}
},
created () {
this.phone = this.$route.query.phone
this.secretPhone = this.phone.replace(this.phone.substr(3, 4), '****')
this.mobile = this.secretPhone
},
beforeRouteEnter (to, from, next) { // 禁止当前页刷新
if (from.name !== 'login') {
next(vm => {
vm.$router.push({
path: '/login'
})
})
return
}
next()
},
components: {
XInput,
Group,
XButton,
XHeader,
Scroll,
Timer
}
}



